$main-font: "Roboto", sans-serif;
$main-blue-color: #00acdc;
$product-card-width: 200px;
$product-card-height: 403px;
$gap-between-product-cards-x: 48px;
$gap-between-product-cards-y: 50px;
$products-container-width:
  ($product-card-width * 4)+($gap-between-product-cards-x*3);
$products-container-height:
  ($product-card-height *2) + ($gap-between-product-cards-y *1);
